import React, { useState } from "react";
import { Modal, Button, Form, Input } from "antd";
import { FiEdit, FiDownload } from "react-icons/fi";
import { IoTrashOutline } from "react-icons/io5";

export default function CallCampaignActions({ list }) {
  const [editListForm] = Form.useForm();
  const [newName, setNewName] = useState(list.name);
  const [submittingForm, setSubmittingForm] = useState(false);
  const [isModalVisible, setIsModalVisible] = useState(false);

  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
    setTimeout(() => {
      editListForm.resetFields();
      setNewName(list.name);
    }, 300);
  };

  return (
    <>
      <div className="list-actions-container">
        <div className="list-actions-item" onClick={showModal}>
          <FiEdit size="18" />
          Edit
        </div>
        <div className="list-actions-item">
          <FiDownload size="18" />
          CSV
        </div>
        <div className="list-actions-item list-actions-item__delete">
          <IoTrashOutline size="18" />
          Delete
        </div>
      </div>

      {/* Edit List Modal */}
      <Modal
        title="Edit List"
        visible={isModalVisible}
        closable={false}
        wrapClassName="create-modal list-modal"
        footer={[
          <Button className="text-capitalize" key="back" onClick={handleCancel}>
            Cancel
          </Button>,
          <Button
            className="text-capitalize"
            key="submit"
            type="primary"
            htmlType="submit"
            loading={submittingForm}
            onClick={handleOk}
            disabled={newName === list.name}
          >
            Update List
          </Button>
        ]}
      >
        <Form
          form={editListForm}
          layout="vertical"
          initialValues={{
            name: list.name
          }}
          onFieldsChange={(changedFields, allFields) => {
            setNewName(changedFields[0].value);
          }}
        >
          <Form.Item name="name" label="List Name">
            <Input />
          </Form.Item>
        </Form>
      </Modal>
    </>
  );
}
