import React, { useState } from "react";
import { Button, Modal } from "antd";
import { VscArchive } from "react-icons/vsc";

export default function ArchiveCampaign({ trigger, campaignId }) {
  const [isModalVisible, setIsModalVisible] = useState(false);

  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  return (
    <>
      {trigger === "button" && (
        <Button onClick={showModal}>Archive Campaign</Button>
      )}

      {trigger === "dropdown" && (
        <a className="table-actions__menu-item" onClick={showModal}>
          <VscArchive className="mr-1" />
          Archive Campaign
        </a>
      )}

      <Modal
        title="Archive Campaign"
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={handleCancel}
        okText="Yes"
        cancelText="No"
        closable={false}
      >
        <p className="lg">
          You are about to archive this campaign. No further actions will be
          allowed on archived campaigns. Do you want to continue?
        </p>
      </Modal>
    </>
  );
}
