import React, { useState } from "react";
import ArchiveCampaign from "./actions/ArchiveCampaign";
import PauseCampaign from "./actions/PauseCampaign";
import ResumeCampaign from "./actions/ResumeCampaign";
import CompleteCampaign from "./actions/CompleteCampaign";
import DownloadReport from "./actions/DownloadReport";
import { Menu, Dropdown } from "antd";
import { IoEllipsisHorizontal } from "react-icons/io5";
import { assignCampaignActions } from "../utils";

export default function CallCampaignActions({ campaignStatus }) {
  const [actions] = useState(assignCampaignActions(campaignStatus));

  const tableActionsMenu = (
    <Menu>
      {actions.includes("archive") && (
        <Menu.Item key="0">
          <ArchiveCampaign trigger="dropdown" />
        </Menu.Item>
      )}
      {actions.includes("complete") && (
        <Menu.Item key="1">
          <CompleteCampaign trigger="dropdown" />
        </Menu.Item>
      )}
      {actions.includes("download") && (
        <Menu.Item key="2">
          <DownloadReport trigger="dropdown" />
        </Menu.Item>
      )}
      {actions.includes("pause") && (
        <Menu.Item key="3">
          <PauseCampaign trigger="dropdown" />
        </Menu.Item>
      )}
      {actions.includes("resume") && (
        <Menu.Item key="4">
          <ResumeCampaign trigger="dropdown" />
        </Menu.Item>
      )}
    </Menu>
  );

  return (
    <Dropdown
      overlay={tableActionsMenu}
      arrow
      placement="bottomRight"
      trigger={["click"]}
    >
      <a
        className="table-actions__trigger"
        onClick={(e) => e.preventDefault()}
        tabIndex="0"
      >
        <IoEllipsisHorizontal size={18} />
      </a>
    </Dropdown>
  );
}
