import React, { useEffect, useState } from "react";
import axios from "axios";
import CallDetailsDropdown from "./CallDetailsDropdown";
import TableFilter from "./TableFilter";
import TableSearch from "./TableSearch";
import { TableLoader } from "./loaders";
import { parseISOString, useDebounce } from "../utils";
import { format } from "date-fns";
import { IoChevronDown } from "react-icons/io5";

export default function CallDetailsTable({ campaignId }) {
  const [callDetails, setCallDetails] = useState([]);
  const [callDetailsLoading, setCallDetailsLoading] = useState(true);
  const [isCallDetailsVisible, setIsCallDetailsVisible] = useState(false);
  const [filters, setFilters] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");
  const debouncedSearchTerm = useDebounce(searchTerm, 500);

  const getCallDetails = () => {
    setCallDetailsLoading(true);
    axios
      .get("https://run.mocky.io/v3/f9ae1b75-bda2-4e22-8937-297e735fb646")
      .then(
        (res) => {
          setCallDetails(res.data.response);
          setTimeout(() => setCallDetailsLoading(false), 350);
        },
        (err) => {
          console.log(err);
        }
      );
  };

  const toggleCallDetails = () => {
    if (isCallDetailsVisible) {
      setIsCallDetailsVisible(false);
    } else {
      setIsCallDetailsVisible(true);
      getCallDetails();
    }
  };

  const applyFilters = (filterArray) => {
    setFilters(filterArray);
    document.querySelector(".table-filters__dropdown").classList.remove("show");
    // Call API with URL:
    // filterArray.length > 0
    //   ? alert(`${baseURL}/campaigns?status=${filterArray.join(",")}`)
    //   : alert(`${baseURL}/campaigns`);
  };

  return (
    <>
      <div className="call-details-trigger">
        <a
          tabIndex="0"
          className="round-button position-relative"
          onClick={toggleCallDetails}
        >
          {isCallDetailsVisible ? "Hide" : "View"}&nbsp;Call Details
          <IoChevronDown
            size="18"
            className={`ml-1 ${isCallDetailsVisible ? "rotated" : ""}`}
          />
        </a>
      </div>
      <div
        className={`call-details-container ${
          isCallDetailsVisible ? "show" : ""
        }`}
      >
        <h2 className="mb-3">Campaign Call Details</h2>
        <div className="campaigns-page__filter-section container mb-3">
          <TableFilter
            title="Calls"
            filterOptions={[
              {
                label: "Completed",
                value: "completed"
              },
              {
                label: "Busy",
                value: "busy"
              },
              {
                label: "No Answer",
                value: "no-answer"
              },
              {
                label: "Failed",
                value: "failed"
              }
            ]}
            filters={filters}
            setFilters={setFilters}
            applyFilters={applyFilters}
          />
          <TableSearch
            setSearchTerm={setSearchTerm}
            placeholder="Search Numbers & Names"
          />
        </div>
        <div className="call-details-filter"></div>
        <table id="callDetailsTable" width="100%">
          <thead>
            <tr>
              <td width="9%">Number</td>
              <td width="10%">Name</td>
              <td width="12%">Total Call Duration</td>
              <td width="22%">Time</td>
              <td width="8%">Staus</td>
              <td width="8%">Digits</td>
              <td width="30%">Recording</td>
              <td width="1%"></td>
            </tr>
          </thead>
          <tbody>
            {callDetailsLoading ? (
              <TableLoader rows={10} cols={8} />
            ) : (
              <>
                {callDetails.map((call) => (
                  <tr key={call.data.call_sid}>
                    <td>{call.data.number}</td>
                    <td>{call.data.name}</td>
                    <td>{call.data.duration || "null"}</td>
                    <td>
                      {format(
                        parseISOString(call.data.start_time),
                        "do LLLL y, hh:mm a"
                      )}
                    </td>
                    <td className="text-capitalize">{call.data.status}</td>
                    <td>{call.data.digit || "null"}</td>
                    <td>
                      <audio controls={true}>
                        <source
                          src="https://file-examples-com.github.io/uploads/2017/11/file_example_MP3_700KB.mp3"
                          type="audio/mpeg"
                        />
                      </audio>
                    </td>
                    <td>
                      {call.data.start_time && (
                        <CallDetailsDropdown callData={call.data} />
                      )}
                    </td>
                  </tr>
                ))}
              </>
            )}
          </tbody>
        </table>
        <div className="page-count">
          <p>Showing 1 - 10 / 100</p>
        </div>
      </div>
    </>
  );
}
