import React, { useState } from "react";
import {
  Modal,
  Button,
  Form,
  Input,
  DatePicker,
  TimePicker,
  Select,
  Radio,
  Checkbox,
  InputNumber
} from "antd";
import format from "date-fns/format";
import axios from "axios";
import { GoPlus } from "react-icons/go";
const { Option } = Select;

export default function CreateCallCampaign() {
  const [createCallCampaignForm] = Form.useForm();
  const [retriesEnabled, setRetriesEnabled] = useState(false);
  const [submittingForm, setSubmittingForm] = useState(false);
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [lists, setLists] = useState([]);
  const [listsLoading, setListsLoading] = useState(false);
  const [callerId, setCallerId] = useState([]);
  const [callerIdLoading, setCallerIdLoading] = useState(false);
  const [flows, setFlows] = useState([]);
  const [flowsLoading, setFlowsLoading] = useState(false);

  const getLists = () => {
    setListsLoading(true);
    axios
      .get("https://run.mocky.io/v3/29342f69-2eea-4120-8ca2-9f4bbf5d55d6")
      .then(
        (res) => {
          setLists(res.data.response);
          setListsLoading(false);
        },
        (err) => {
          console.log(err);
        }
      );
  };

  const getCallerId = () => {
    setCallerIdLoading(true);
    axios
      .get("https://run.mocky.io/v3/8286200a-d410-4ae3-8689-ff69b394dfff")
      .then(
        (res) => {
          setCallerId(res.data.response);
          setCallerIdLoading(false);
        },
        (err) => {
          console.log(err);
        }
      );
  };

  const getFlows = () => {
    setFlowsLoading(true);
    axios
      .get("https://run.mocky.io/v3/14d4e364-2fba-499c-85c6-a9ab6e285be8")
      .then(
        (res) => {
          setFlows(res.data.response);
          setFlowsLoading(false);
        },
        (err) => {
          console.log(err);
        }
      );
  };

  const showModal = () => {
    getLists();
    getCallerId();
    getFlows();
    setIsModalVisible(true);
  };

  const createDateTimeISO = (date, time) => {
    return `${format(date, "yyyy-MM-dd")}T${format(time, "HH:mm")}:00+5:30}`;
  };

  const formatSubmission = (formData) => {
    return {
      name: formData.name,
      type: formData.type,
      caller_id: formData.caller_id,
      url: formData.flow_url,
      lists: [formData.list],
      schedule: {
        // send_at: createDateTimeISO(
        //   formData.start_date._d,
        //   formData.start_time._d
        // ),
        // end_at: createDateTimeISO(formData.end_date._d, formData.end_time._d)
      },
      retries: {
        number_of_retries: formData.number_of_retries,
        interval_min: formData.interval_min,
        mechanism: formData.mechanism,
        on_status: formData.on_status
      }
    };
  };

  const handleOk = () => {
    createCallCampaignForm.validateFields();
    // setSubmittingForm(true);
    console.log(formatSubmission(createCallCampaignForm.getFieldValue()));
    // Send form data
    // then:
    // setIsModalVisible(false);
    // setSubmittingForm(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
    setTimeout(() => {
      createCallCampaignForm.resetFields();
      setRetriesEnabled(false);
    }, 300);
  };

  const onRetriesChange = (e) => {
    if (e.target.value > 0) {
      setRetriesEnabled(true);
    } else {
      setRetriesEnabled(false);
    }
  };

  return (
    <>
      <Button type="primary" onClick={showModal}>
        <GoPlus />
        Create Call Campaign
      </Button>

      <Modal
        title="Create Call Campaign"
        visible={isModalVisible}
        width={860}
        closable={false}
        centered={true}
        wrapClassName="create-modal create-call-campaign-modal"
        footer={[
          <Button className="text-capitalize" key="back" onClick={handleCancel}>
            Cancel
          </Button>,
          <Button
            className="text-capitalize"
            key="submit"
            type="primary"
            htmlType="submit"
            loading={submittingForm}
            onClick={handleOk}
          >
            Create Call Campaign
          </Button>
        ]}
      >
        <Form
          form={createCallCampaignForm}
          layout="vertical"
          initialValues={{
            number_of_retries: 0,
            on_status: ["busy", "no_answer", "failed"],
            type: "trans",
            call_duplicate_numbers: "no"
          }}
        >
          <div className="form-grid">
            <div className="span-2">
              <Form.Item
                name="name"
                label="Campaign Name"
                rules={[
                  ({ getFieldValue }) => ({
                    validator(rule, value) {
                      if (!value) {
                        return Promise.reject("Please enter a Campaign Name");
                      }
                      if (value.length < 3) {
                        return Promise.reject(
                          "Campaign Name must be at least 3 characters"
                        );
                      }
                      return Promise.resolve();
                    }
                  })
                ]}
              >
                <Input />
              </Form.Item>
            </div>
            <div className="span-2">
              <Form.Item
                name="list"
                label="Contact List"
                rules={[
                  {
                    required: true,
                    message: "Please select a Contact List"
                  }
                ]}
              >
                <Select placeholder="Select" loading={listsLoading}>
                  {lists.map((item) => (
                    <Option value={item.data.sid} key={item.data.sid}>
                      {item.data.name}
                    </Option>
                  ))}
                </Select>
              </Form.Item>
            </div>
            <div className="span-1">
              <Form.Item
                name="start_date"
                label="Start Date"
                rules={[
                  {
                    required: true,
                    message: "Please select a Start Date"
                  }
                ]}
              >
                <DatePicker />
              </Form.Item>
            </div>
            <div className="span-1">
              <Form.Item
                name="start_time"
                label="Start Time"
                rules={[
                  {
                    required: true,
                    message: "Please select a Start Time"
                  }
                ]}
              >
                <TimePicker use12Hours format="h:mm a" />
              </Form.Item>
            </div>
            <div className="span-1">
              <Form.Item
                name="end_date"
                label="End Date"
                rules={[
                  {
                    required: true,
                    message: "Please select an End Date"
                  }
                ]}
              >
                <DatePicker />
              </Form.Item>
            </div>
            <div className="span-1">
              <Form.Item
                name="end_time"
                label="End Time"
                rules={[
                  {
                    required: true,
                    message: "Please select an End Time"
                  }
                ]}
              >
                <TimePicker use12Hours format="h:mm a" />
              </Form.Item>
            </div>
            <div className="span-2">
              <Form.Item
                name="caller_id"
                label="Caller ID"
                rules={[
                  {
                    required: true,
                    message: "Please select a Caller ID"
                  }
                ]}
              >
                <Select placeholder="Select" loading={callerIdLoading}>
                  {callerId.map((item) => (
                    <Option value={item.data.sid} key={item.data.sid}>
                      {item.data.phone_number}
                    </Option>
                  ))}
                </Select>
              </Form.Item>
            </div>
            <div className="span-2">
              <Form.Item
                name="flow_url"
                label="Flow"
                rules={[
                  {
                    required: true,
                    message: "Please select a Flow"
                  }
                ]}
              >
                <Select placeholder="Select" loading={flowsLoading}>
                  {flows.map((item) => (
                    <Option value={item.data.voice_flow_url} key={item.data.id}>
                      {item.data.name}
                    </Option>
                  ))}
                </Select>
              </Form.Item>
            </div>
            <div className="span-4">
              <Form.Item name="number_of_retries" label="Number of Retries">
                <Radio.Group onChange={onRetriesChange}>
                  <Radio value={0}>0</Radio>
                  <Radio value={1}>1</Radio>
                  <Radio value={2}>2</Radio>
                  <Radio value={3}>3</Radio>
                </Radio.Group>
              </Form.Item>
            </div>
            <div className="span-1">
              <Form.Item name="interval_mins" label="Retry Interval in Minutes">
                <InputNumber disabled={!retriesEnabled} />
              </Form.Item>
            </div>
            <div className="span-1">
              <Form.Item name="mechanism" label="Retry Mechanism">
                <Select disabled={!retriesEnabled}>
                  <Option />
                </Select>
              </Form.Item>
            </div>
            <div className="span-2">
              <Form.Item label="Retry Calls with Status" name="on_status">
                <Checkbox.Group disabled={!retriesEnabled}>
                  <Checkbox checked value="busy">
                    Busy
                  </Checkbox>
                  <Checkbox checked value="no_answer">
                    No Answer
                  </Checkbox>
                  <Checkbox checked value="failed">
                    Failed
                  </Checkbox>
                </Checkbox.Group>
              </Form.Item>
            </div>
            <div className="span-2">
              <Form.Item label="Call Type" name="type">
                <Radio.Group>
                  <Radio value="trans">Transactional</Radio>
                  <Radio value="promo" disabled>
                    Promotional
                  </Radio>
                </Radio.Group>
              </Form.Item>
            </div>
            <div className="span-2">
              <Form.Item
                label="Call Duplicate Numbers"
                name="call_duplicate_numbers"
              >
                <Radio.Group>
                  <Radio value="yes">Yes</Radio>
                  <Radio value="no">No</Radio>
                </Radio.Group>
              </Form.Item>
            </div>
            <div className="span-4">
              <p>
                Note: Later modifications of the list will not affect the
                campaign. Campaigns will execute only the numbers present in the
                list at the time of campaign creation
              </p>
            </div>
          </div>
        </Form>
      </Modal>
    </>
  );
}
