import React, { useState } from "react";
import { Modal, Button, Form, Input, Upload, message } from "antd";
import { GoPlus } from "react-icons/go";
import { FaFileCsv } from "react-icons/fa";
const { Dragger } = Upload;

export default function CreateList() {
  const [createListForm] = Form.useForm();
  const [fileName, setFileName] = useState("");
  const [submittingForm, setSubmittingForm] = useState(false);
  const [isModalVisible, setIsModalVisible] = useState(false);

  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    createListForm.validateFields();
    console.log(createListForm.getFieldValue());
    // setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  const fileUploadProps = {
    name: "file",
    accept: ".csv",
    multiple: false,
    maxCount: 1,
    showUploadList: false,
    onChange(info) {
      const { status } = info.file;
      if (status !== "uploading") {
        console.log(info.file);
      }
      if (status === "done") {
        setFileName(info.file.name);
        message.success(`${info.file.name} file uploaded successfully.`);
      } else if (status === "error") {
        setFileName(info.file.name);
        message.error(`${info.file.name} file upload failed.`);
      }
    },
    onDrop(e) {
      console.log("Dropped files", e.dataTransfer.files);
    }
  };

  return (
    <>
      <Button type="primary" onClick={showModal}>
        <GoPlus />
        Create List
      </Button>

      <Modal
        title="Create List"
        visible={isModalVisible}
        closable={false}
        wrapClassName="create-modal list-modal"
        footer={[
          <Button className="text-capitalize" key="back" onClick={handleCancel}>
            Cancel
          </Button>,
          <Button
            className="text-capitalize"
            key="submit"
            type="primary"
            htmlType="submit"
            loading={submittingForm}
            onClick={handleOk}
          >
            Create List
          </Button>
        ]}
      >
        <Form form={createListForm} layout="vertical">
          <div className="mb-3">
            <Form.Item
              name="name"
              label="List Name"
              rules={[
                {
                  required: true,
                  message: "Please enter a name for your List"
                }
              ]}
            >
              <Input placeholder="Enter Name" />
            </Form.Item>
          </div>
          <div className="position-relative">
            <div className="sample-csv">
              <a className="inline-link" tabIndex="0">
                Download Sample CSV
              </a>
            </div>
            <Form.Item name="upload" label="Import Contacts">
              <Dragger {...fileUploadProps}>
                {fileName.length > 0 ? (
                  <>
                    <p className="ant-upload-drag-icon">
                      <FaFileCsv size="48" color="lightgrey" />
                    </p>
                    <p>
                      {fileName}
                      <br />
                      <br />
                      <a className="inline-link">Remove File</a>
                    </p>
                  </>
                ) : (
                  <>
                    <p className="ant-upload-drag-icon">
                      <FaFileCsv size="48" color="lightgrey" />
                    </p>
                    <p className="ant-upload-hint">
                      Drag and drop CSV File here
                      <br />
                      or
                      <br />
                      <a className="inline-link">Browse Files</a>
                    </p>
                  </>
                )}
              </Dragger>
            </Form.Item>
          </div>
        </Form>
      </Modal>
    </>
  );
}
