import React, { useState } from "react";
import { Button, Modal } from "antd";
import { IoArrowDownCircleOutline } from "react-icons/io5";

export default function DownloadReport({ trigger, report_url }) {
  const [isModalVisible, setIsModalVisible] = useState(false);

  const showModal = () => {
    setIsModalVisible(true);
  };

  return (
    <>
      {trigger === "button" && (
        <Button type="primary" onClick={showModal}>
          Download Report
        </Button>
      )}

      {trigger === "dropdown" && (
        <a className="table-actions__menu-item" onClick={showModal}>
          <IoArrowDownCircleOutline className="mr-1" />
          Download Report
        </a>
      )}

      <Modal
        title="Download Report"
        visible={isModalVisible}
        onCancel={() => setIsModalVisible(false)}
        footer={[
          <Button type="primary" onClick={() => setIsModalVisible(false)}>
            Close
          </Button>
        ]}
        closable={false}
      >
        <p className="lg">
          The download has been initiated. Please wait before the file starts
          downloading to your system.
        </p>
      </Modal>
    </>
  );
}
