import React, { useState } from "react";
import { Button, Modal } from "antd";
import { IoPauseCircleOutline } from "react-icons/io5";

export default function PauseCampaign({ trigger, campaignId }) {
  const [isModalVisible, setIsModalVisible] = useState(false);

  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    // Archive Campaign
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  return (
    <>
      {trigger === "button" && (
        <Button type="primary" onClick={showModal}>
          Pause Campaign
        </Button>
      )}
      {trigger === "dropdown" && (
        <a className="table-actions__menu-item" onClick={showModal}>
          <IoPauseCircleOutline className="mr-1" />
          Pause Campaign
        </a>
      )}
      <Modal
        title="Pause Campaign"
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={handleCancel}
        okText="Yes"
        cancelText="No"
        closable={false}
      >
        <p className="lg">
          You are about to pause the on-going campaign. It will not have any
          impact on the ongoing calls, but further calls will not be triggered
          until the campaign is resumed. Do you want to continue?
        </p>
      </Modal>
    </>
  );
}
