import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import TableFilter from "./TableFilter";
import TableSearch from "./TableSearch";
import CreateCallCampaign from "./CreateCallCampaign";
import CallCampaignActions from "./CallCampaignActions";
import SortIcon from "./icons/SortIcon";
import { TableLoader } from "./loaders";
import axios from "axios";
import {
  IoCheckmarkCircle,
  IoCloseCircle,
  IoTimeOutline
} from "react-icons/io5";
import { format } from "date-fns";
import { baseURL, statusText, parseISOString, useDebounce } from "../utils";

export default function CallCampaignsTable() {
  const [campaignsShown, setCampaignsShown] = useState([]);
  const [campaignsLoading, setCampaignsLoading] = useState(true);
  const [offset, setOffset] = useState(0);
  const [filters, setFilters] = useState([]);
  const [sortBy, setSortBy] = useState("");
  const [sortOrder, setSortOrder] = useState("");
  const [searchTerm, setSearchTerm] = useState("");
  const debouncedSearchTerm = useDebounce(searchTerm, 500);

  useEffect(() => {
    getAllCampaigns(offset, filters, sortBy, sortOrder, debouncedSearchTerm);
  }, [offset, filters, sortBy, sortOrder, debouncedSearchTerm]);

  // Sort
  useEffect(() => {
    if (sortBy.length > 0) {
      alert(`${baseURL}/campaigns?sort_by=${sortBy}:${sortOrder}`);
    }
  }, [sortBy, sortOrder]);

  // Search
  useEffect(() => {
    if (debouncedSearchTerm.length > 3) {
      alert(`${baseURL}/campaigns?name=${debouncedSearchTerm}`);
    }
  }, [debouncedSearchTerm]);

  // Get All Campaigns
  const getAllCampaigns = (
    offset,
    filters,
    sortBy,
    sortOrder,
    debouncedSearchTerm
  ) => {
    setCampaignsLoading(true);
    axios
      .get("https://run.mocky.io/v3/a2ae4f05-c6d0-4c74-b080-619cf494b596", {
        params: {
          limit: 10,
          offset,
          status: filters.join(","),
          sortby: `${sortBy}:${sortOrder}`,
          name: debouncedSearchTerm
        }
      })
      .then(
        (res) => {
          setCampaignsShown(res.data.response);
          setTimeout(() => setCampaignsLoading(false), 350);
        },
        (err) => {
          console.log(err);
        }
      );
  };

  // Filter
  const applyFilters = (filterArray) => {
    setFilters(filterArray);
    document.querySelector(".table-filters__dropdown").classList.remove("show");
    // Call API with URL:
    filterArray.length > 0
      ? alert(`${baseURL}/campaigns?status=${filterArray.join(",")}`)
      : alert(`${baseURL}/campaigns`);
  };

  const applySort = (e) => {
    if (sortBy.length === 0) {
      setSortBy(e.target.dataset.sortName);
      setSortOrder("asc");
    }
    if (sortBy.length > 0 && sortBy !== e.target.dataset.sortName) {
      setSortBy(e.target.dataset.sortName);
      setSortOrder("asc");
    }
    if (sortBy.length > 0 && sortBy === e.target.dataset.sortName) {
      setSortOrder(sortOrder === "" || sortOrder === "desc" ? "asc" : "desc");
    }
  };

  return (
    <section className="campaings-page__table-section">
      <div className="campaigns-page__filter-section container">
        <TableFilter
          title="Call Campaigns"
          filterOptions={[
            {
              label: "Scheduled",
              value: "Created"
            },
            {
              label: "In Progress",
              value: "InProgress"
            },
            {
              label: "Paused",
              value: "Paused"
            },
            {
              label: "Completed",
              value: "Completed"
            },
            {
              label: "Archived",
              value: "Archived"
            }
          ]}
          filters={filters}
          setFilters={setFilters}
          applyFilters={applyFilters}
        />
        <div className="d-flex">
          <TableSearch
            setSearchTerm={setSearchTerm}
            placeholder="Search Call Campaigns"
          />
          <CreateCallCampaign />
        </div>
      </div>

      <div className="campaings-page__table-container">
        <table id="callCampaignsTable" width="100%">
          <thead>
            <tr>
              <td width="20%">
                <a
                  data-sortable
                  data-sort-name="name"
                  onClick={(e) => applySort(e)}
                >
                  Campaign Name
                  <SortIcon
                    order={`${
                      sortBy === "name"
                        ? sortOrder === "asc"
                          ? "asc"
                          : "desc"
                        : ""
                    }`}
                  />
                </a>
              </td>
              <td width="15%">Campaign Type</td>
              <td width="24%">
                <a
                  data-sortable
                  data-sort-name="send_at"
                  onClick={(e) => applySort(e)}
                >
                  Scheduled Time
                  <SortIcon
                    order={`${
                      sortBy === "send_at"
                        ? sortOrder === "asc"
                          ? "asc"
                          : "desc"
                        : ""
                    }`}
                  />
                </a>
              </td>
              <td width="15%">Status</td>
              <td width="25%">Performance</td>
              <td width="1%">&nbsp;</td>
            </tr>
          </thead>
          <tbody>
            {campaignsLoading ? (
              <TableLoader rows={10} cols={6} />
            ) : (
              <>
                {campaignsShown.map((campaign, i) => (
                  <tr key={campaign.data.id}>
                    {/* Campaign Name */}
                    <td>
                      <Link to={`/campaign/${campaign.data.id}`}>
                        {`${campaign.data.name}`}
                      </Link>
                    </td>

                    {/* Campaign Type */}
                    <td>{campaign.data.type === "trans" && "Transactional"}</td>

                    {/* Schedule Time */}
                    <td>
                      {format(
                        parseISOString(campaign.data.schedule.send_at),
                        "do LLLL y, hh:mm a"
                      )}
                    </td>

                    {/* Status */}
                    <td className="text-uppercase">
                      {statusText(campaign.data.status)}
                    </td>

                    {/* Performance */}
                    <td>
                      <span className="performance-icon text-success mr-3">
                        <IoCheckmarkCircle color="#2DCF00" />
                        {campaign.data.stats["completed"]}
                      </span>
                      <span className="performance-icon text-danger mr-3">
                        <IoCloseCircle color="#F62525" />
                        {campaign.data.stats["failed"] +
                          campaign.data.stats["failed-dnd"]}
                      </span>
                      <span className="performance-icon">
                        <IoTimeOutline />
                        {campaign.data.stats["created"] +
                          campaign.data.stats["in-progress"] +
                          campaign.data.stats["retry"] +
                          campaign.data.stats["retrying"]}
                      </span>
                    </td>

                    {/* Actions */}
                    <td>
                      <CallCampaignActions
                        campaignStatus={campaign.data.status}
                      />
                    </td>
                  </tr>
                ))}
              </>
            )}
          </tbody>
        </table>
        <div className="page-count">
          <p>Showing 1 - 10 / 100</p>
        </div>
      </div>
    </section>
  );
}
