import React from "react";
import ContentLoader from "react-content-loader";

export const TableLoader = ({ rows, cols }) => {
  const tdLoader = (
    <ContentLoader
      speed={2}
      width={"100%"}
      height={30}
      backgroundColor="#f3f3f3"
      foregroundColor="#ecebeb"
    >
      <rect x="0" y="0" rx="0" ry="0" width="95%" height="25" />
    </ContentLoader>
  );
  return (
    <>
      {new Array(rows).fill("").map((row, index) => (
        <tr key={`row${index}`}>
          {new Array(cols).fill("").map((col, index) => (
            <td key={`col${index}`}>{tdLoader}</td>
          ))}
        </tr>
      ))}
    </>
  );
};

export const CampaignSummaryHeaderLoader = () => (
  <ContentLoader
    speed={2}
    width={"100%"}
    height={100}
    backgroundColor="#f3f3f3"
    foregroundColor="#ecebeb"
  >
    <rect x="40" y="0" width="200" height="40" />
    <rect x="40" y="50" width="120" height="30" />
    {/* <rect x="600" y="0" width="200" height="35" /> */}
    <rect
      x={`calc(${window.innerWidth - 240})`}
      y="0"
      width="180"
      height="35"
    />
    <rect
      x={`calc(${window.innerWidth - 440})`}
      y="0"
      width="180"
      height="35"
    />
  </ContentLoader>
);
