import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import ArchiveCampaign from "../components/actions/ArchiveCampaign";
import PauseCampaign from "../components/actions/PauseCampaign";
import ResumeCampaign from "../components/actions/ResumeCampaign";
import CompleteCampaign from "../components/actions/CompleteCampaign";
import DownloadReport from "../components/actions/DownloadReport";
import CallDetailsTable from "../components/CallDetailsTable";
import { BiArrowBack } from "react-icons/bi";
import { HiOutlineRefresh } from "react-icons/hi";
import { Button } from "antd";
import { format } from "date-fns";
import { assignCampaignActions, parseISOString } from "../utils";
import { PieChart } from "react-minimal-pie-chart";
import { CampaignSummaryHeaderLoader } from "../components/loaders";

export default function CallCampaignSummary({ match }) {
  const [isLoading, setIsLoading] = useState(false);
  const [campaignDetails, setCampaignDetails] = useState({});
  const [campaignActions, setCampaignActions] = useState([]);

  const getCampaignSummary = () => {
    setIsLoading(true);
    axios
      .get("https://run.mocky.io/v3/09ad4560-8685-4325-afe4-b942366bc94d")
      .then(
        (res) => {
          setCampaignDetails(res.data.response[0].data);
          setCampaignActions(
            assignCampaignActions(res.data.response[0].data.status)
          );
          setIsLoading(false);
        },
        (err) => {
          console.log(err);
        }
      );
  };

  useEffect(() => {
    getCampaignSummary();
  }, []);

  const calculateTotalNumbers = (stats) => {
    let sum = 0;
    Object.keys(stats).map((key) => {
      sum += stats[key];
    });
    return sum;
  };

  return (
    <div className="campaign-summary__container container">
      {isLoading ? (
        <CampaignSummaryHeaderLoader />
      ) : (
        <div className="campaign-summary__header">
          <div className="d-flex">
            <Link to="/" className="campaign-summary__back">
              <BiArrowBack className="mr-3" size="27" />
            </Link>
            <div>
              <h1>{campaignDetails.name}</h1>
              <div className="campaign-summary__status">
                {campaignDetails.status === "Created" && (
                  <span className="status-badge status-badge-scheduled">
                    Scheduled
                  </span>
                )}
                {campaignDetails.status === "InProgress" && (
                  <span className="status-badge status-badge-in-progress">
                    In Progress
                  </span>
                )}
                {campaignDetails.status === "Paused" && (
                  <span className="status-badge status-badge-paused">
                    Paused
                  </span>
                )}
                {campaignDetails.status === "Completed" && (
                  <span className="status-badge status-badge-completed">
                    Completed
                  </span>
                )}
                {campaignDetails.status === "Failed" && (
                  <span className="status-badge status-badge-failed">
                    Failed
                  </span>
                )}
              </div>
            </div>
          </div>
          {campaignActions.length > 0 && (
            <div className="campaign-summary__actions">
              {campaignActions.includes("archive") && (
                <ArchiveCampaign trigger="button" />
              )}
              {campaignActions.includes("pause") && (
                <PauseCampaign trigger="button" />
              )}
              {campaignActions.includes("resume") && (
                <ResumeCampaign trigger="button" />
              )}
              {campaignActions.includes("complete") && (
                <CompleteCampaign trigger="button" />
              )}
              {campaignActions.includes("download") && (
                <DownloadReport trigger="button" />
              )}
            </div>
          )}
        </div>
      )}
      <div className="grid-2 mb-4">
        <div className="campaign-summary__details">
          <div className="grid-2">
            <div className="details-item span-2">
              <h4>List Name</h4>
              <a href="#">March customer list_March customer list</a>
            </div>
            <div className="details-item span-2">
              <h4>Flow Name</h4>
              <a href="#">campaignflow_campaignflow_campaignflow</a>
            </div>
            <div className="details-item">
              <h4>Start Time</h4>
              {campaignDetails.schedule && (
                <p>
                  {format(
                    parseISOString(campaignDetails.schedule.send_at),
                    "dd/MM/yyyy, hh:mm a"
                  )}
                </p>
              )}
            </div>
            <div className="details-item">
              <h4>End Time</h4>
              {campaignDetails.schedule && (
                <p>
                  {format(
                    parseISOString(campaignDetails.schedule.end_at),
                    "dd/MM/yyyy, hh:mm a"
                  )}
                </p>
              )}
            </div>
            <div className="details-item">
              <h4>Caller ID</h4>
              <p>{campaignDetails.caller_id}</p>
            </div>
            <div className="details-item">
              <h4>Call Type</h4>
              <p>
                {campaignDetails.type === "trans" && "Transactional"}
                {campaignDetails.type === "promo" && "Promotional"}
              </p>
            </div>
            {campaignDetails.retries && (
              <>
                <div className="details-item">
                  <h4>No. of Retries</h4>
                  <p>{campaignDetails.retries.number_of_retries}</p>
                </div>
                <div className="details-item">
                  <h4>Retry Interval</h4>
                  <p>{`${campaignDetails.retries.interval_mins} ${
                    campaignDetails.retries.interval_mins === 1
                      ? "minute"
                      : "minutes"
                  }`}</p>
                </div>
                <div className="details-item">
                  <h4>Retry Mechanism</h4>
                  <p>{campaignDetails.retries.mechanism}</p>
                </div>
                <div className="details-item">
                  <h4>Retry Calls with Status</h4>
                  <p className="text-capitalize">
                    {campaignDetails.retries.on_status.join(", ")}
                  </p>
                </div>
              </>
            )}
            <div className="details-item">
              <h4>Call Duplicate Numbers</h4>
              <p>{campaignDetails.call_duplicate_numbers ? "Yes" : "No"}</p>
            </div>
          </div>
        </div>

        <div className="campaign-summary__stats">
          <div className="campaign-summary__stats-header">
            <h3 className="font-weight-semibold mb-0">Campaign Stats</h3>
            <Button>
              <HiOutlineRefresh size="17" />
            </Button>
          </div>
          <div className="campaign-summary__stats-chart">
            <div className="chart-grid">
              <div className="chart-container">
                <PieChart
                  data={[
                    { title: "Scheduled", value: 20, color: "#13B3C6" },
                    { title: "In Progress", value: 15, color: "#34B94A" },
                    { title: "Retry", value: 20, color: "#006FA7" },
                    { title: "Retrying", value: 20, color: "#3394E3" },
                    { title: "Failed", value: 20, color: "#FB686D" },
                    { title: "Failed DND", value: 20, color: "#D95458" },
                    { title: "Completed", value: 20, color: "#007F53" }
                  ]}
                  lineWidth={40}
                  paddingAngle={1}
                  animate
                />
                <div className="chart-total-numbers">
                  <h3 className="font-weight-semibold mb-2">
                    {campaignDetails.stats &&
                      calculateTotalNumbers(campaignDetails.stats)}
                  </h3>
                  <p>Total Numbers</p>
                </div>
              </div>
              {campaignDetails.stats && (
                <div className="chart-legend">
                  <div className="chart-legend-item">
                    <div
                      className="chart-legend-color"
                      style={{ backgroundColor: "#13B3C6" }}
                    />
                    <div className="chart-legend-name">Scheduled</div>
                    <div className="chart-legend-value">{`: ${campaignDetails.stats["created"]}`}</div>
                  </div>
                  <div className="chart-legend-item">
                    <div
                      className="chart-legend-color"
                      style={{ backgroundColor: "#34B94A" }}
                    />
                    <div className="chart-legend-name">In Progress</div>
                    <div className="chart-legend-value">{`: ${campaignDetails.stats["in-progress"]}`}</div>
                  </div>
                  <div className="chart-legend-item">
                    <div
                      className="chart-legend-color"
                      style={{ backgroundColor: "#006FA7" }}
                    />
                    <div className="chart-legend-name">Retry</div>
                    <div className="chart-legend-value">{`: ${campaignDetails.stats["retry"]}`}</div>
                  </div>
                  <div className="chart-legend-item">
                    <div
                      className="chart-legend-color"
                      style={{ backgroundColor: "#3394E3" }}
                    />
                    <div className="chart-legend-name">Retrying</div>
                    <div className="chart-legend-value">{`: ${campaignDetails.stats["retrying"]}`}</div>
                  </div>
                  <div className="chart-legend-item">
                    <div
                      className="chart-legend-color"
                      style={{ backgroundColor: "#FB686D" }}
                    />
                    <div className="chart-legend-name">Failed</div>
                    <div className="chart-legend-value">{`: ${campaignDetails.stats["failed"]}`}</div>
                  </div>
                  <div className="chart-legend-item">
                    <div
                      className="chart-legend-color"
                      style={{ backgroundColor: "#D95458" }}
                    />
                    <div className="chart-legend-name">Failed DND</div>
                    <div className="chart-legend-value">{`: ${campaignDetails.stats["failed-dnd"]}`}</div>
                  </div>
                  <div className="chart-legend-item">
                    <div
                      className="chart-legend-color"
                      style={{ backgroundColor: "#007F53" }}
                    />
                    <div className="chart-legend-name">Completed</div>
                    <div className="chart-legend-value">{`: ${campaignDetails.stats["completed"]}`}</div>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>

      {/* Call Details */}
      <CallDetailsTable campaignId={match.params.id} />
    </div>
  );
}
