import React, { useRef, useState } from "react";
import { Button } from "antd";
import { VscChevronDown } from "react-icons/vsc";

export default function TableFilter({
  title,
  filterOptions,
  filters,
  setFilters,
  applyFilters
}) {
  const [count, setCount] = useState(0);
  const [checkedValues, setCheckedValues] = useState([]);
  const [dropdownVisible, setDropdownVisible] = useState(false);
  const dropdownRef = useRef();

  const onFilterChecked = (e) => {
    if (e.target.checked === true) {
      setCheckedValues([...checkedValues, e.target.value]);
      setCount(count + 1);
    } else {
      setCheckedValues(
        checkedValues.filter((value) => {
          return value !== e.target.value;
        })
      );
      setCount(count - 1);
    }
  };

  const clearAll = () => {
    document.querySelectorAll("input[name='filters']").forEach((el) => {
      el.checked = false;
    });
    setCheckedValues([]);
    setFilters([]);
    setCount(0);
  };

  return (
    <div className="table-filters">
      <div
        className="table-filters__trigger"
        onClick={() => setDropdownVisible(!dropdownVisible)}
        tabIndex="0"
      >
        <span className="mr-1">
          {filters.length === 0 && `All ${title}`}
          {filters.length > 0 && `${title} (${filters.join(", ")})`}
        </span>
        <VscChevronDown size="18" />
      </div>
      <div
        ref={dropdownRef}
        className={`table-filters__dropdown ${dropdownVisible ? "show" : ""}`}
      >
        <div className="table-filters__dropdown-filters">
          <div className="mb-2 font-weight-semibold">Filters:</div>
          <form>
            {filterOptions.map((filter, index) => (
              <div className="input-group" key={index}>
                <input
                  type="checkbox"
                  name="filters"
                  value={filter.value}
                  id={filter.value}
                  onChange={onFilterChecked}
                />
                <label htmlFor={filter.value}>{filter.label}</label>
              </div>
            ))}
          </form>
        </div>
        <div className="table-filters__dropdown-footer">
          <div className="text-grey">{`${count} Selected`}</div>
          <div className="d-flex align-items-center">
            {count > 0 && (
              <a className="mr-3" onClick={() => clearAll()} tabIndex="0">
                Clear All
              </a>
            )}
            <Button type="primary" onClick={() => applyFilters(checkedValues)}>
              Apply
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
}
