import React, { useState } from "react";
import { Menu, Dropdown, Tooltip } from "antd";
import { parseISOString } from "../utils";
import { format } from "date-fns";
import { BiCopy } from "react-icons/bi";
import { IoEllipsisHorizontal } from "react-icons/io5";

export default function CallDetailsDropdown({ callData }) {
  const [copyText, setCopyText] = useState("Copy to Clipboard");

  const copySID = () => {
    navigator.clipboard.writeText(callData.call_sid);
    setCopyText("Copied");
    setTimeout(() => setCopyText("Copy to Clipboard"), 3000);
  };

  const callDetailsMenu = (
    <Menu>
      {callData && (
        <>
          <h4 className="font-weight-semibold">More Details</h4>
          <p>
            Start Time :{" "}
            {format(parseISOString(callData.start_time), "hh:mm a")}
          </p>
          <p>
            End Time :{" "}
            {(callData.end_time &&
              format(parseISOString(callData.end_time), "hh:mm a")) ||
              "null"}
          </p>
          <p>Caller ID : {callData.caller_id}</p>
          <p>Direction : {callData.direction}</p>
          <p>
            Call SID&nbsp;:&nbsp;
            {callData.call_sid.substr(1, 14)}...&nbsp;
            <Tooltip title={copyText}>
              <BiCopy className="copy-sid" onClick={copySID} />
            </Tooltip>
          </p>
        </>
      )}
    </Menu>
  );

  return (
    <Dropdown
      overlay={callDetailsMenu}
      overlayClassName="call-details-dropdown"
      arrow
      placement="bottomRight"
      trigger={["click"]}
    >
      <a
        className="table-actions__trigger"
        onClick={(e) => e.preventDefault()}
        tabIndex="0"
      >
        <IoEllipsisHorizontal size={18} />
      </a>
    </Dropdown>
  );
}
