import React, { useState } from "react";
import { Button, Modal } from "antd";
import { IoCheckmarkCircleOutline } from "react-icons/io5";

export default function CompleteCampaign({ trigger, campaignId }) {
  const [isModalVisible, setIsModalVisible] = useState(false);

  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    // Archive Campaign
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  return (
    <>
      {trigger === "button" && (
        <Button onClick={showModal}>Mark As Completed</Button>
      )}
      {trigger === "dropdown" && (
        <a className="table-actions__menu-item" onClick={showModal}>
          <IoCheckmarkCircleOutline className="mr-1" />
          Mark as Completed
        </a>
      )}
      <Modal
        title="Mark As Completed"
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={handleCancel}
        okText="Yes"
        cancelText="No"
        closable={false}
      >
        <p className="lg">
          You are about to mark this campaign as completed. The remaining calls
          will not be processed. Do you want to continue?
        </p>
      </Modal>
    </>
  );
}
