import React, { useEffect, useState } from "react";
import axios from "axios";
import TableSearch from "./TableSearch";
import CreateList from "./CreateList";
import ListActions from "./ListActions";
import SortIcon from "./icons/SortIcon";
import { TableLoader } from "./loaders";
import { format } from "date-fns";
import { parseISOString } from "../utils";

export default function ListsTable() {
  const [lists, setLists] = useState([]);
  const [listsLoading, setListsLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");
  const [sortBy, setSortBy] = useState("");
  const [sortOrder, setSortOrder] = useState("");

  const getLists = () => {
    setListsLoading(true);
    axios
      .get("https://run.mocky.io/v3/29342f69-2eea-4120-8ca2-9f4bbf5d55d6")
      .then(
        (res) => {
          setLists(res.data.response);
          setTimeout(() => setListsLoading(false), 350);
        },
        (err) => {
          console.log(err);
        }
      );
  };

  useEffect(() => {
    getLists();
  }, []);

  const applySort = (e) => {
    if (sortBy.length === 0) {
      setSortBy(e.target.dataset.sortName);
      setSortOrder("asc");
    }
    if (sortBy.length > 0 && sortBy !== e.target.dataset.sortName) {
      setSortBy(e.target.dataset.sortName);
      setSortOrder("asc");
    }
    if (sortBy.length > 0 && sortBy === e.target.dataset.sortName) {
      setSortOrder(sortOrder === "" || sortOrder === "desc" ? "asc" : "desc");
    }
  };

  return (
    <>
      <div className="campaigns-page__filter-section container">
        <div />
        <div className="d-flex">
          <TableSearch
            setSearchTerm={setSearchTerm}
            placeholder="Search Lists"
          />
          <CreateList />
        </div>
      </div>

      <div className="campaings-page__table-container">
        <table id="listsTable" width="100%">
          <thead>
            <tr>
              <td width="25%">
                <a
                  data-sortable
                  data-sort-name="name"
                  onClick={(e) => applySort(e)}
                >
                  List Name
                  <SortIcon
                    order={`${
                      sortBy === "name"
                        ? sortOrder === "asc"
                          ? "asc"
                          : "desc"
                        : ""
                    }`}
                  />
                </a>
              </td>
              <td width="22%">Number of Contacts</td>
              <td width="20%">
                <a
                  data-sortable
                  data-sort-name="date_created"
                  onClick={(e) => applySort(e)}
                >
                  Date Created
                  <SortIcon
                    order={`${
                      sortBy === "date_created"
                        ? sortOrder === "asc"
                          ? "asc"
                          : "desc"
                        : ""
                    }`}
                  />
                </a>
              </td>
              <td width="35%">&nbsp;</td>
            </tr>
          </thead>
          <tbody>
            {listsLoading ? (
              <TableLoader rows={10} cols={4} />
            ) : (
              lists?.map((list, index) => (
                <tr key={list.data.sid}>
                  <td>{list.data.name}</td>
                  <td>{list.data.contact_count || "0"}</td>
                  <td>
                    {format(
                      parseISOString(list.data.date_created),
                      "do LLLL y"
                    )}
                  </td>
                  <td>
                    <ListActions list={list.data} />
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>
    </>
  );
}
