import React from "react";
import { Route } from "react-router-dom";
import PageHeader from "../components/PageHeader";
import CampaignsNav from "../components/CampaignsNav";
import CallCampaignsTable from "../components/CallCampaignsTable";
import ListsTable from "../components/ListsTable";
import ContactsTable from "../components/ContactsTable";

export default function HomePage({ location }) {
  return (
    <div>
      <PageHeader path={location.pathname} />
      <Route path="/" exact>
        <CampaignsNav />
        <CallCampaignsTable />
      </Route>
      <Route path="/lists" component={ListsTable} />
      <Route path="/contacts" component={ContactsTable} />
    </div>
  );
}
